.hidden {
    display: none;
}
.form {
    font-family: $mainText;
    background: linear-gradient(to bottom, #f5f5f5, #ffffff 9%);
    padding: 30px 27px;

    a {
        font-family: $mainText;
        text-decoration: none;
    }

    .form__icon {
        color: $secondaryColor;
        margin-right: 10px;
    }

    .form__arrow--design, .form__arrow--fill, .form__arrow--share {
        color: $primaryColor;
        border: none;
        background-color: transparent;
    }

    .form__arrow--collapse {
        transform: rotate(180deg);
    }

    .form__title {
        font-family: $mainText;
        font-weight: 900;
        text-transform: uppercase;
        font-size: 18px;
        color: $primaryColor;
        margin: 10px 0;
    }

    .form__closed {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: baseline;
    }

    .form__open {
        padding-bottom: 20px;
    }

    .form__sections {
        border-bottom: 1px solid $borderLineColor;
    }

    

// --------- Design design ---------

    .palettesContainer {
        display: grid;
        grid-template-columns: 1fr 1fr;
        
        & h3{
            text-transform: uppercase;
            padding: 0;
            padding-top: 0.75rem;
            padding-left: 1.3125rem;
            font-size: 0.875rem;
            grid-column: 1/2;
        
            @media all and (min-width:$tablet) {
                padding-left: 0;
            }
        }
        .paletteChoices {
            grid-column: 2/3;
            display: grid;
            grid-template-columns: 30px 90px;
            grid-template-rows: 1.5625rem 1.25rem 1.5625rem 1.25rem 1.5625rem;
            justify-content: end;
            padding-top: 0.9375rem;
      
            .radio_button {
                justify-self: center;
                align-self: center;
            }
            .rb_one {
                grid-column: 1/2;
            }

            .rb_two {
                grid-row: 3;
                grid-column: 1/2;
            }

            .rb_three {
                grid-row: 5;
                grid-column: 1/2;
            }

            .palette-grid{
                display: grid;
                grid-template-columns: repeat(3,30px);
                grid-template-rows: auto;
                justify-content: end;
                grid-column: 2/3;
            }

            .grid-one{
                grid-row: 1;
                
                    .palOne_colOne {
                        background-color: $formText;
                    }

                    .palOne_colTwo {
                        background-color: $palete1Color2;
                    }

                    .palOne_colThree {
                        background-color: $borderColor;
                    }
            }
            
            .grid-two{
                grid-row: 3;

                    .palTwo_colOne {
                        background-color: $palete2Color1;
                    }

                    .palTwo_colTwo {
                        background-color: $palete2Color2;
                    }

                    .palTwo_colThree {
                        background-color: $palete2Color3;
                    }
            }

            .grid-three{
                    grid-row: 5;
                    
                    .palThree_colOne {  
                        background-color:$palete3Color1;
                    }
            
                    .palThree_colTwo {
                        background-color:$palete3Color2;
                    }

                    .palThree_colThree {
                        background-color:$palete3Color3;
                    }   
            }
        }
    } 
    

    .paletteStyle2{
        .infoStyle{
            border-left: 5px solid $palete2Color1;

                &--name{
                    color:$palete2Color2;
                }
        }
    
        .buttonStyle{ 
            color: $palete2Color3;
            border: 2px solid $palete2Color3;
        }
    }

    .paletteStyle3{
        .infoStyle{
            border-left: 5px solid $palete3Color2;

                &--name{
                    color:$palete3Color1;
                }
        }
    
        .buttonStyle{ 
            color: $palete3Color3;
            border: 2px solid $palete3Color3;
        }
    }
}


// --------- Section fill ---------

.form__fill {
    margin-bottom: 0px;

    label {
        color: $formText;

        span {
            color: $primaryColor;
        }
    }

    ::placeholder {
        color: $placeholderText;
        padding: 5px;
    }

    input {
        border: 1px solid $borderColor;
        margin: 5px 0 20px 0;
        height: 30px;
        width: 100%;
    }

    .form__flex {
        display: flex;
        flex-direction: column;
        font-size: 14px;
        line-height: 20px;
        padding-bottom: 20px;
    }

    .photo__upload {
        display: flex;
        flex-direction: row;
    }

    .photo__upload--btn {
        width: 140px;
        height: 30px;
        background-color: $secondaryColor;
        color: white;
        font-family: $mainText;
        font-size: 14px;
        line-height: 0;
        border-radius: 3px;
        box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.20);
        margin: 10px 0 20px;
    }

    .thumbnail {
        width: 30px;
        height: 30px;
        border: 1px solid $borderColor;
        box-sizing: border-box;
        margin: 10px 0 0 20px;
    }
}


